  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-4
    .titulo-principal.color-acento-contenido(data-aos="flip-up")
      .titulo-principal__numero
        span 1
      h1 Mercadeo
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p En el estudio del comportamiento del mercado, se deben considerar factores que permitan analizar los productos de manera que respondan eficazmente a las necesidades de la poblacin. Estos factores se derivan de los intereses y beneficios que surgen del uso de los productos. Lo anterior implica la colaboracin de toda la organizacin para alcanzar los objetivos propuestos. Se invita a explorar el componente formativo a travs de la observacin detallada del concepto de mercadeo que se presentar a continuacin.
    
    figure.mb-4(data-aos="zoom-in")
      .video
        iframe(width="560" height="315" src="https://www.youtube.com/embed/lpS6sY69J1E?si=9k3wjtXDLZduPKEc" title="YouTube video player" frameborder="0" allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture" allowfullscreen)

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Con base en lo expuesto, el mercadeo se consolida como un conjunto de acciones y actividades encaminadas a satisfacer las necesidades de los clientes, generando un valor adecuado a cambio. Esto implica una investigacin continua del mercado para comprender las necesidades del cliente y lo que estn dispuestos a hacer para satisfacerlas.
    
    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-4
      .bloque-texto-g__img(
        :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/img1.jpg')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 En la actualidad, el mercadeo es un componente fundamental para el crecimiento de cualquier negocio o empresa, independientemente de su tamao. De hecho, los negocios emergentes que exploran el mercado estn destinados a integrar el <i>marketing</i> como parte esencial de su estrategia, ya que les proporciona resistencia y fluidez en el mbito comercial. El mercadeo no solo se trata de qu se dice, sino tambin de cmo se comunica, de transmitir las cualidades sobresalientes de un producto y explicar por qu la gente debera elegirlo por encima de la competencia.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Para muchos especialistas dedicados al mercadeo, tambin conocido como <i>marketing</i>, este sola limitarse a anuncios, folletos o comunicados de prensa. Sin embargo, en la actualidad, con la proliferacin de las redes sociales, el <i>marketing</i> ha experimentado una transformacin notable. Ahora, una estrategia efectiva de <i>marketing</i> se materializa a travs de plataformas como Instagram, Twitter o Facebook, donde se destacan las virtudes del producto y se brinda acceso directo al cliente mientras navega por sus redes sociales.
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .cajon.color-primario.p-4.mb-4      
          p Una forma esencial de llevar a cabo el mercadeo es a travs del <i>marketing</i>, definido como la interseccin entre la empresa y el cliente; la convergencia de los intereses de la empresa con las necesidades del comprador. A un nivel ms profundo, implica el proceso de comprender a los clientes, establecer y mantener relaciones slidas con ellos. En ltima instancia, el mercadeo se convierte en la clave del xito de cualquier organizacin, sin importar su tamao. Podra decirse que todas las empresas necesitan del <i>marketing</i> para prosperar, ya que cmo pueden vender sus productos y servicios en el mercado si nadie los conoce?"
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p As, la comprensin del mercadeo se adentra en el conocimiento profundo de los clientes, involucrando el compromiso de todos los trabajadores interesados en demostrar a los clientes potenciales su dedicacin a resolver problemas reales. No se limitan a actuar como si les importara; realmente les importa, y esto se evidencia en sus acciones. Su enfoque genuino es ayudar a los clientes a mejorar sus vidas mediante contenido relevante, experiencias significativas y una pasin palpable. Y, si tienen la oportunidad, lo hacen a travs de los productos o servicios que ofrecen.

        //- p As, la comprensin del mercadeo se adentra en el conocimiento profundo de los clientes, involucrando el compromiso de todos los trabajadores interesados en demostrar a los clientes potenciales su dedicacin a resolver problemas reales. No se limitan a actuar como si les importara; realmente les importa, y esto se evidencia en sus acciones. Su enfoque genuino es ayudar a los clientes a mejorar sus vidas mediante contenido relevante, experiencias significativas y una pasin palpable. Y, si tienen la oportunidad, lo hacen a travs de los productos o servicios que ofrecen.

    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas Investigacin de mercados
        
    .col-lg-12
      p A travs de la investigacin de mercados, se realiza un estudio con propsitos especficos dirigidos a los potenciales clientes del mercado. Este proceso implica analizar las necesidades particulares de un amplio grupo de clientes para identificar nuevas oportunidades y determinar cmo satisfacer esas necesidades en cada segmento de mercado objetivo. Adems, sugiere cmo disear un producto o servicio para cubrir esas necesidades.
      p La investigacin de mercados recopila una variedad de datos importantes, como el precio que los clientes estn dispuestos a pagar, quines son los competidores directos, cmo se presentar y describir el producto para atraer a los clientes hacia el negocio, y la personalidad del producto. En otras palabras, permite profundizar en los siguientes aspectos:

