    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p La investigacin de mercados no solo consiste en recopilar informacin, sino tambin en aprovecharla para tomar decisiones estratgicas. Los datos recopilados a travs de diversos instrumentos permiten extraer y utilizar los comentarios y la inteligencia de los clientes. Esto orienta la decisin sobre qu productos fabricar en el futuro o cmo mejorar los productos existentes para satisfacer mejor las necesidades del mercado.
        p Adems, herramientas como los grupos de enfoque, las encuestas a clientes y la recopilacin de datos de usuarios en lnea han trado grandes beneficios al campo del mercadeo. Estas herramientas proporcionan informacin valiosa sobre la base de clientes en constante evolucin, asegurando que la marca se comunique con ellos de manera efectiva. En resumen, el estudio de mercado implica investigar y comprender mejor a los clientes para desarrollar productos o servicios que satisfagan sus deseos y necesidades de manera ms efectiva.
    
    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas Tipos de mercadeo

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-5
        figure
          img(src='@/assets/curso/temas/tema1/img7.png', alt='Imagen decorativa')
      .col-lg-7
        p Cuando se habla de los tipos de mercadeo, es comn referirse al <i>marketing</i>, el cual puede dividirse aproximadamente en mtodos fuera de lnea y en lnea, tambin conocidos como digitales. El <i>marketing</i> fuera de lnea engloba la publicidad tradicional, que incluye anuncios impresos, radiales y televisivos, as como la participacin en eventos, ferias y conferencias. Tambin puede involucrar estrategias de <i>marketing</i> boca a boca.
        p La mayora de las empresas optan por una combinacin de mtodos de <i>marketing</i> tradicional y en lnea. Sin embargo, en la actualidad, el enfoque est cada vez ms orientado hacia el <i>marketing</i> en lnea. Esto se debe a que los consumidores pasan ms tiempo en lnea y el <i>marketing</i> digital ofrece numerosas ventajas en trminos de velocidad y eficacia.
    
    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas #[i Marketing] tradicional
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-7.col-12
        p Hablar de lo convencional nos lleva a considerar el <i>marketing</i> tradicional, que comprende cualquier tipo de estrategia de <i>marketing</i> realizada sin el uso de Internet. Esto incluye anuncios publicados en peridicos impresos en lugar de en lnea, noticias presentadas en revistas fsicas en lugar de digitales, y el uso de correo postal en lugar de correo electrnico. Sin embargo, una de las herramientas ms poderosas y efectivas en el <i>marketing</i> tradicional sigue siendo la publicidad televisiva, que tiene un alcance masivo.
        p Un ejemplo destacado de este enfoque es la promocin de cierta marca de cerveza, que cautiv a los espectadores con un anuncio de televisin que se convirti en un fenmeno de la cultura popular. Esta campaa publicitaria recibi numerosos premios y gener un aumento significativo en las ventas. Esto ejemplifica el poder del <i>marketing</i> tradicional y cmo puede generar impacto a travs de medios impresos y televisivos.
      .col-lg-5.col-12
        figure
          img(src='@/assets/curso/temas/tema1/img114.png', alt='Imagen decorativa')

