        p Las campaas de <i>marketing</i> tradicionales tienen la ventaja de perdurar en el dominio pblico, convirtindose en elementos icnicos y familiares para millones de personas. Una de las campaas ms clebres del <i>marketing</i> tradicional fue la de Coca-Cola con el eslogan 'La pausa que refresca'.
