        p Es la matriz de evaluacin de factores internos (MEFI o EFI) la cual identifica y evala las relaciones entre las reas funcionales del negocio. Herramienta de formulacin de estrategias en la que se resume y evala las fortalezas y debilidades de las reas prcticas del negocio.
        p La matriz EFI es intuitiva, por lo que su enfoque cientfico no debe interpretarse como una tcnica poderosa. La bsqueda de las fortalezas comprende que es lo que el negocio hace mejor o qu valor agregado lo diferencia de su competencia. En las debilidades perciba las reas de la empresa que podra mejorar o nivelar frente a la competencia.
        p Es una herramienta de anlisis estratgico utilizada para explorar el contexto interno de la empresa y hallar las fortalezas y debilidades que se encuentran en las reas de la empresa.  Las opciones para analizar el ambiente interno van desde un anlisis DOFA, o una evaluacin comparativa, anlisis adicionales, son algunas de las herramientas a las que podemos recurrir para evaluar los factores internos.

    .row.justify-content-center.align-items-center.mb-4
