          img(src='@/assets/curso/temas/tema1/tabla2_.svg', alt='Imagen decorativa')
    
    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas La Matriz de poltica direccional de Shell (DPM) 

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Representan las perspectivas de rentabilidad del sector empresarial y se encuentra la capacidad competitiva de una empresa. La rentabilidad del sector empresarial incluye el tamao del mercado, el crecimiento esperado, la falta de competencia. Por otro lado, la capacidad competitiva de la empresa est determinada por el volumen de ventas, la reputacin de los productos, la confiabilidad del servicio y los precios competitivos. En cualquier campo de la matriz implica diferentes decisiones estratgicas. La matriz suele utilizarse para responder ante situaciones de crisis del negocio.
        p Las estrategias que plantea Shell en su matriz son:

    .row.justify-content-center.align-items-center.mb-4.BG01
      .col-md-12.p-3
        SlyderA(tipo='b' class='bgslyder').mb-4
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Desinvertir
              p Distingue prdidas con flujos de caja inciertos, en los que es necesario liquidar o mover sus bienes, deberan venderse ya que no es probable que la situacin mejore.
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img48.png', alt='Imagen decorativa')
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Retiro por etapas
              p En posicin competitiva dbil, en un mercado de bajo crecimiento, con muy pocas posibilidades de generar flujos de efectivo. Por tanto, han de eliminarse gradualmente. El flujo de efectivo debe invertirse en empresas ms rentables.
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img49.png', alt='Imagen decorativa')
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Doble o renuncie
              p Invierta ms para aprovechar los matices que presenta el mercado o mejor abandona el negocio.
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img50.png', alt='Imagen decorativa')
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Custodio
              p La empresa tiene que lidiar con la situacin obteniendo ayuda de otras o saliendo de la escena para enfocarse ms en otros negocios atractivos.
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img51.png', alt='Imagen decorativa')
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Esfurcese ms
              p Necesitan recursos adicionales para fortalecer sus capacidades. Por lo tanto, las empresas se esfuerzan ms por explotar a fondo las perspectivas comerciales.
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img52.png', alt='Imagen decorativa')
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Generacin de efectivo
              p Expansin a otros escenarios, pueden continuar con sus operaciones, al menos para generar fuertes flujos de caja y utilidades satisfactorias. 
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img53.png', alt='Imagen decorativa')
          .row
            .col-md-4.mb-4.mb-md-0.p-4
              h4 Crecimiento del lder
              p Mediante la Innovacin de los productos puede apoyar el crecimiento generando fondos para incentivar la novedad en el producto o servicio. Haga crecer el mercado enfocando solo suficientes recursos aqu.
            .col-md-8
              figure
                img(src='@/assets/curso/temas/tema1/img54.png', alt='Imagen decorativa')

    .row.mb-4
    <li><strong>Liderazgo en el mercado</strong></li>
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Se concentran los principales recursos recibiendo mxima prioridad.

    .row.justify-content-center.mb-4
      .col-8.p-2
        .titulo-sexto.color-acento-contenido.mb-0(data-aos="fade-right")
          h5 Figura 4.
          span #[i Matriz  de liderazgo en el mercado]
    .row.justify-content-center.mb-4
      .col-lg-8.desktop
        figure
          img(src='@/assets/curso/temas/tema1/tabla3.svg', alt='Imagen que relaciona un tabla indicativa de la matriz de liderazgo en el mercado, destacando el atractivo, promedio y el no atractivo.')
      .col-lg-8.movil
        figure
          img(src='@/assets/curso/temas/tema1/tabla3_1.svg', alt='Imagen que relaciona un tabla indicativa de la matriz de liderazgo en el mercado, destacando el atractivo, promedio y el no atractivo.')

    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas Matriz ADL

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Dicha matriz se denomina Matriz de Condicin Estratgica de Arthur D. Little, la cual se fundamenta en el ciclo de vida del producto, tiene la intencin de ayudar a las empresas a administrar su coleccin de negocios de productos como una cartera. Al igual que otras matrices de planificacin de cartera, la matriz ADL representa los diversos negocios de una empresa en una matriz bidimensional. Es una metodologa estructurada para la consideracin de estrategias que dependen del ciclo de vida de la industria. Algunos elementos esenciales a tener presente son:
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        .row.justify-content-center.align-items-center
          .col-lg-10
            .tarjeta-avatar-b
              .tarjeta-avatar-b__img
                img(src='@/assets/curso/temas/tema1/img55.svg' alt='AvatarTop')
              .tarjeta.tarjeta--verde2
                .p-4
                  br
                  br
                  p La matriz ADL a menudo se asocia con la planificacin estratgica a nivel de unidad de negocio, sin embargo, funciona igual de bien cuando se aplica a lneas de productos o a nivel de un producto individual. 
        .row.justify-content-center.align-items-center
          .col-lg-10
            .tarjeta-avatar-b
              .tarjeta-avatar-b__img
                img(src='@/assets/curso/temas/tema1/img56.svg' alt='AvatarTop')
              .tarjeta.tarjeta--verde
                .p-4
                  br
                  br
                  p El enfoque ADL utiliza las dimensiones de la evaluacin del entorno y el negocio: Posicin competitiva y madurez de la industria.
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p La posicin competitiva: Estrategias de los competidores, fuerza de la posicin competitiva, categorizando cada segmento de la empresa de acuerdo con la posicin, el dominio, fuerza y que pueda ser favorable, sostenible o dbil.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12.desktop
        ImagenInfografica.color-acento-botones.mb-4
          template(v-slot:imagen)
            figure
              img(src='@/assets/curso/temas/tema1/img58.png', alt='') 
          .tarjeta.bg2.p-3(x="16%" y="22%" numero="+")
            p #[b Favorable:] goza de ventajas competitivas en varios segmentos del mercado para tener la libertad de moverse en el mercado como grandes lderes de sus negocios.
          .tarjeta.bg2.p-3(x="73.5%" y="24%" numero="+")
            p #[b Sostenible:] la posicin en el mercado general es pequea o de nicho, ya sea geogrfica o definida por producto.
          .tarjeta.bg2.p-3(x="92%" y="14%" numero="+")
            p #[b Dominante:] liderazgo tecnolgico protegido.
          .tarjeta.bg2.p-3(x="33%" y="58.5%" numero="+")
            p #[b Dbil:] prdida de cuota de mercado. La lnea de negocio es demasiado pequea para mantener la rentabilidad.
          .tarjeta.bg2.p-3(x="66.5%" y="65%" numero="+")
            p #[b Fuerte:] la participacin de mercado es estable, tiene la libertad de elegir estrategias y accionar sin que el mercado se vea en riesgo o amenazado por el asecho de los competidores.

      .col-lg-12.movil
        figure
          img(src='@/assets/curso/temas/tema1/img58_.svg', alt='')

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Los elementos antes relacionados son: 
    .bg4.p-5.mb-4
      .row.justify-content-center.align-items-center
        .col-lg-10
          ul.lista-ul
            li
              i.fas.fa-check
              |	Madurez de la industria: la madurez de la industria consiste en el desarrollo de la industria a travs de fases, donde considera al segmento para alcanzar la madurez del escenario. Para ello se describe la naturaleza de esta madurez.
            li
              i.fas.fa-check
              |	Principio: caracterizada por un rpido crecimiento del mercado, muy poca competencia, nueva tecnologa, alta inversin y altos precios.
            li
              i.fas.fa-check
              |	Crecimiento: las ventas aumentan, existen pocos competidores y la empresa obtiene recompensas por lanzar un nuevo producto al mercado.
            li
              i.fas.fa-check
              |	Madurez: la participacin en el mercado es estable, hay una base de clientes constante, hay muchos competidores y se dedica energa a diferenciarse de los competidores.
            li
              i.fas.fa-check
              |	Envejecimiento: se empieza a abandonar el Mercado, las ventas disminuyen, la demanda se reduce, se vuelve costoso mantenerse por encima de la competencia o mantener el nivel por lo que se convierte en algunas de las causas para que el negocio empiece a desaparecer.
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .tarjeta.color-secundario.p-3
          .row.justify-content-around.align-items-center
            .col-auto
