    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas Matriz de Igor Ansoff

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Titulada tambin matriz de crecimiento del mercado, ayuda a comprender y a evaluar la estrategia de desarrollo de marketing. Es una forma simple y efectiva de considerar las opciones de avance estratgico. Piense en las fortalezas del negocio y cules estrategias de crecimiento le permiten impulsar, motivar y explotar sus fortalezas. Tenga en cuenta la diversificacin, puesto que es un terreno desconocido, es mejor avanzar en escenarios conocidos que correr mayor riesgo de fracasar en la transformacin radical.
        p Las estrategias que plantea Ansoff en su matriz son:

    AcordionA.mb-4(tipo="a" clase-tarjeta="tarjeta tarjeta--gris")
      .row(titulo="Penetracin del mercado")
        .col-md-6.mb-4.mb-md-0
          p Desarrollo de las ventas de productos existentes en el mercado. Una fuerte participacin de mercado sugiere que es probable que se obtenga un mejor rendimiento al ampliar la gama de productos y servicios que puede ofrecer al mercado. 
        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema1/img59.png', alt='Imagen decorativa')
      .row(titulo="Desarrollo de productos")
        .col-md-6.mb-4.mb-md-0
          p Establecer nuevos productos para llevarlos al mercado es una estrategia interesante si tiene una fuerte participacin de mercado. La estrategia puede ser una razn adecuada para adquirir otra empresa de producto o servicio siempre que sea relevante para su mercado.
        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema1/img60.png', alt='Imagen decorativa')
      .row(titulo="Desarrollo de mercado")
        .col-md-6.mb-4.mb-md-0
          p Los nuevos mercados tambin pueden significar nuevas subdivisiones dentro del mercado. Ayuda a mantenerse razonablemente junto al mercado que conoce y que lo conoce a usted.
        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema1/img61.png', alt='Imagen decorativa')
      .row(titulo="Diversificacin")
        .col-md-6.mb-4.mb-md-0
          p Llevar nuevos productos a nuevos mercados, aunque este cuadrante es de alto riesgo, no solo porque no conoce los productos, sino porque tampoco conoce los nuevos mercados y es muy probable que la estrategia implique trabajar en nuevos canales de distribucin y rutas al mercado. 
        .col-md-6
          figure
            img(src='@/assets/curso/temas/tema1/img62.png', alt='Imagen decorativa')
