    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas Matriz BCG

    .row.justify-content-center.mb-4
      .col-lg-6
        p La Matriz BCG de Boston o conocida como el anlisis de #[i Boston Consulting Group] o como la Matriz de crecimiento-participacin. Representa una forma til de comprender y evaluar sus diferentes oportunidades de productos y servicios existentes, adems de ser una herramienta para valorar los productos existentes y en desarrollo en trminos de su potencial de mercado y, por lo tanto, implica acciones estratgicas para productos y servicios en cada categora.
        p Es un grfico creado para ayudar a las organizaciones con el anlisis de sus unidades de negocio o lneas de productos. Contribuye a que las empresas asignen recursos asertivamente y se utiliza como herramienta analtica en #[i marketing] de marca, gestin estratgica y anlisis de cartera. La matriz BCG formada por cuatro campos se basa en la observacin de que las unidades de negocios de la empresa se clasifican en cuatro categoras fusionadas en crecimiento y participacin de mercado. Examina diferentes negocios en su cartera en funcin de las tasas de crecimiento. En trminos generales es un anlisis comparativo del potencial comercial y la evaluacin del entorno.
      .col-lg-6
        figure
          img(src='@/assets/curso/temas/tema1/img63.png', alt='Imagen decorativa')
    
