          .tarjeta.bg2.p-3(x="66.6%" y="50.7%" numero="2")
            p #[b Situacin problemtica:] son productos que tienen un potencial de mercado grande y creciente pero que tienen una participacin de mercado baja, normalmente porque son productos nuevos o porque la aplicacin an no se ha detectado ni se ha actuado en consecuencia. 
          .tarjeta.bg2.p-3(x="56.7%" y="67%" numero="3")
            p #[b Vaca:] se denomina as, porque la intencin es ordear los rendimientos de inversiones anteriores que establecieron una buena distribucin y participacin de mercado para el producto. Los productos en este cuadrante necesitan actividad de mantenimiento y proteccin, junto con una buena gestin de costos.
          .tarjeta.bg2.p-3(x="66.8%" y="67%" numero="4")
            p #[b Perro:] no tiene sentido desarrollar productos o servicios en este cuadrante. Muchas organizaciones descontinan productos servicios que consideran son de esta categora, en cuyo caso, considere el impacto potencial en la recuperacin de costos generales. 
