      .col-lg-6
        p La agricultura sigue siendo una importante fuente de empleo en el pas, ya que emplea la mano de obra a pesar de los impactos negativos de la pandemia. La agricultura colombiana experiment el mayor crecimiento de cualquier sector en 2020 y continu creciendo en 2021, junto con el sector ganadero.
