        p Segn el documento Balance 2020 y perspectiva 2021 (ao). Infiere que el mercado laboral ha sido uno de los grandes afectados a nivel mundial con la emergencia sanitaria provocada por el COVID-19. Para la vigencia 2020, la organizacin Internacional del trabajo OIT habla de prdidas de puestos de trabajo alrededor de uno 345 millones. En Colombia, la prdida de puestos de trabajo oscila entre los 1,5 millones, observa los elementos a tener presente frente a este balance realizado: 

