    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        .cajon.color-primario.p-4.mb-4
          p Gracias al tamao de su mercado y la extensin de sus recursos naturales: caf, esmeraldas, petrleo y carbn, entre otros. Adems de una reputacin histrica como un deudor ejemplar, Colombia ha experimentado un crecimiento estable y slido durante la mayor parte de las ltimas dos dcadas. Si bien el pas se vio afectado por la cada de los precios del petrleo debido a la pandemia en 2020, Colombia pudo recuperarse en 2021, con un crecimiento del producto interno bruto (PIB) principalmente debido al impulso de las exportaciones de petrleo y al aumento del consumo de los hogares, que debera continuar para impulsar la economa en los prximos aos, apoyado en la continua recuperacin del mercado laboral y la continua implementacin de programas de apoyo fiscal dirigidos a los hogares de bajos ingresos.
