    .row.justify-content-center.align-items-center.mb-4
      .col-lg-10
        p En 2020, las exportaciones colombianas sufrieron la cada de los precios del petrleo y la desaceleracin econmica en sus principales mercados asociados provocada por la pandemia. De hecho, las exportaciones del pas disminuyeron y durante el mismo perodo, las importaciones de bienes tambin disminuyeron. Como resultado, el dficit comercial de Colombia lleg a 7,9 mil millones de dlares en 2020. Segn la Organizacin Mundial del Comercio OMC, las importaciones de servicios en 2020 ascendieron a 9 mil millones de dlares, mucho ms que las exportaciones de servicios. Por lo tanto, el dficit comercial general en 2020 alcanz los 12,4 mil millones de dlares.
    
