        p La importancia del sector servicios ha aumentado en los ltimos aos, convirtindose en la columna vertebral de la economa colombiana El turismo es uno de los componentes ms importantes del sector servicios y ha mostrado un gran dinamismo en los ltimos aos, especialmente en Bogot, Medelln, Cartagena, Cali y Barranquilla. El sector servicios fue el ms afectado durante las primeras etapas de la pandemia, especialmente el turismo, hotelera, la restauracin, el transporte y el entretenimiento. Sin embargo, en 2021, los servicios mostraron una recuperacin significativa a medida que aumentaron las tasas de vacunacin y aument la movilidad de las personas.

