    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p El consumo privado es el principal motor de la recuperacin a medida que repunta el empleo, aunque a un ritmo ms lento que la actividad econmica. Los fuertes precios de las materias primas y la mejora de las perspectivas en los principales socios comerciales clave seguirn apuntalando las exportaciones. Para fomentar el espritu empresarial, se deben reducir los altos costos de registro de las empresas, especialmente para las pequeas empresas.
    
    .bloque-texto-g.color-secundario.p-3.p-sm-4.p-md-5.mb-4
      .bloque-texto-g__img(
        :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/img77.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 La creacin de empleo formal requiere un sector empresarial ms fuerte. El acceso a los mercados debe ser apoyado a travs de nuevas mejoras en la infraestructura de transporte, en particular multimodal, conectividad de transporte de puertos y aduanas, y reduccin de barreras de entrada y competencia en el transporte. Deben eliminarse las barreras no arancelarias distorsionadoras y facilitarse la entrada y el crecimiento de las empresas mediante una mayor dependencia de ventanillas nicas y herramientas en lnea para los procedimientos administrativos.

    .row.justify-content-center.align-items-center.mb-3
      .col-lg-12
        p En la ltima dcada, los resultados educativos siguen siendo mediocres, estos dependen en gran medida de los antecedentes socioeconmicos y muestran grandes diferencias regionales. Mejorar los resultados en la equidad de la educacin y la formacin debe comenzar ampliando el acceso a la educacin de la primera infancia y la escolarizacin de jornada completa, particularmente en las zonas rurales. Los recursos deben reasignarse a regiones vulnerables para que la enseanza sea ms atractiva all. 
        p Los planes de estudios y la financiacin de la educacin y la formacin profesional deben ajustarse en funcin de a las necesidades del mercado laboral, y se debe alentar la participacin de los empleadores en la entrega. Las prcticas dbiles excluyen a muchos del mercado laboral formal. La recuperacin econmica se consolido especialmente en el segundo semestre de 2021, pues la poblacin ms vulnerable se encontr vacunada para ese momento. La aceleracin en la construccin de viviendas y las mejoras en el mercado laboral sern importantes para la recuperacin econmica.
        p Las tasas de crecimiento ms fuertes para la vigencia 2021, se observaron en el entretenimiento, las artes y las actividades recreativas, el comercio mayorista y minorista, informacin y comunicacin; actividades manufactureras y servicios administrativos y de apoyo y actividades tcnicas, cientficas y profesionales. La economa se expandi en un 4,3 %.

