        p El pas tambin es parte de numerosos tratados de libre comercio con Chile, Guatemala, Honduras, El Salvador, Canad, Mxico, Suiza, Noruega, Islandia y Estados Unidos. El tratado de libre comercio con Estados Unidos, ha tenido un impacto especialmente grande en la economa colombiana, dado que Estados Unidos es el mayor socio comercial del pas.

