        p.mb-0 Colombia tiene 15 acuerdos comerciales preferenciales vigentes. La tasa arancelaria promedio ponderada por el comercio es del 6,8 por ciento y estn en vigor 153 medidas no arancelarias. En general, las polticas gubernamentales no interfieren significativamente con la inversin extranjera. El sector financiero se mantiene resistente. Las reformas continan enfocndose y promoviendo el desarrollo de los mercados de capital al mejorar la flexibilidad y la competencia.

    .row.justify-content-center.align-items-center.mb-4
