              p El estudio de la economa se clasifica segn su variabilidad en: la macroeconoma y la microeconoma. Para las empresas un anlisis macro o micro puede ayudar con la toma de decisiones cuando se est pensando en expandir la produccin o insercin en un nuevo mercado. A continuacin, se detalla cada una de estas variables econmicas:
              p #[b La Macroeconoma:] Examina cmo se comporta la economa a gran escala, incluyendo factores que impactan el mercado, el producto interno bruto, la inflacin, el crecimiento econmico y las cifras de desempleo. Comprende cada uno de los elementos y cmo influyen entre s para analizar el lugar que ocupa el negocio dentro del mercado. Los inversionistas utilizan la variable para planificar los futuros movimientos analizando cmo se comporta el Mercado.
    
    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Algunas variables macroeconmicas que se utilizan para medir el estado actual de la economa son: 

    TabsA.color-acento-botones.mb-4
      .tarjeta.color-acento-botones--borde.p-4(titulo="Producto interno bruto (PIB)")
        .row.justify-content-center.align-items-center.mb-4
          .col-lg-12
            h4 Producto interno bruto (PIB)
            p En el ao 2021, el PIB salt un 10,3 por ciento, recuperndose slidamente de una contraccin del 7,3 por ciento en el ao anterior. El sector ms grande y de ms rpido crecimiento de la economa colombiana es el de servicios con un 53% del PIB, en el que incluye: financiamiento, seguros, bienes races y servicios empresariales; servicios comunitarios, sociales y personales; comercio, reparaciones, restaurantes y hoteles y transporte, almacenamiento y comunicaciones. La industria manufacturera y la minera.
            p Otros sectores incluyen: 
