                i.fas.fa-angle-right
                | Gobierno 10 %
              li
                i.fas.fa-angle-right
                | Agricultura, caza, silvicultura y pesca 6,3 %
              li
                i.fas.fa-angle-right
                | Construccin 6 %
              li
                i.fas.fa-angle-right
                | Distribucin de electricidad, gas y agua 4 %


      .tarjeta.color-acento-botones--borde.p-4(titulo="Tasa de inflacin")
        .row.justify-content-center.align-items-center.mb-4
          .col-lg-12
            h4 Tasa de inflacin
            p El ao pasado, la inflacin fue del 5,62 %, la ms alta en 5 aos, no se espera que la inflacin de Colombia regrese al 3 % objetivo del banco central en los prximos 2 aos. La tasa de inflacin anual en Colombia se aceler a 9,23% en abril de 2022 desde 8,53 % en marzo, superando el consenso de mercado de 8,78 %. Las principales presiones al alza provinieron del costo de los alimentos y bebidas no alcohlicas, bebidas alcohlicas y tabaco, vestuario, agua, servicios pblicos, muebles, salud, transporte, ocio y cultura, restauracin y varios.

      .tarjeta.color-acento-botones--borde.p-4(titulo="ndice de precios al consumidor (IPC)")
        .row.justify-content-center.align-items-center.mb-4
          .col-lg-12
            h4 ndice de precios al consumidor (IPC)
            p En Colombia, los tres principales componentes del ndice de precios al consumidor son: 
            ul.lista-ul
              li
                i.fas.fa-angle-right
                | Vivienda y Servicios Pblicos 33,1 %
              li
                i.fas.fa-angle-right
                | Alimentos y Bebidas No Alcohlicas 15 %
              li
                i.fas.fa-angle-right
                | Transporte 12,9 %
            p Otros incluyen: Restaurantes y Hoteles, bienes y servicios miscelneos. Educacin e Informacin y Comunicacin, mobiliario, vestuario y calzado, Recreacin y cultura, Bebidas Alcohlicas y Tabaco y Salud. El IPC cubre la poblacin urbana, incluidas algunas reas metropolitanas, que concentran la mayor poblacin registrada en el pas y presentan el entorno econmico ms dinmico en comparacin con el resto de las ciudades del pas.

      .tarjeta.color-acento-botones--borde.p-4(titulo="ndice de precios al productor (IPP)")
        .row.justify-content-center.align-items-center.mb-4
          .col-lg-12
            h4 ndice de precios al productor (IPP)
            p Mide el cambio promedio en el tiempo de los precios desde el primer nivel de mercadeo para una canasta de productos. Dicho en otras palabras, evala el cambio promedio en el precio de los bienes y servicios vendidos por los fabricantes y productores en el mercado mayorista durante un perodo determinado. La entidad encargada de generar esta informacin es el Departamento Administrativo Nacional de Estadstica (DANE).  Los precios al productor en Colombia aumentaron un 35,65% interanual en abril de 2022, acelerndose desde un aumento del 32,84% en el mes anterior. Fue la inflacin al productor ms alta desde que comenzaron los registros comparables en el 2000. 
      .tarjeta.color-acento-botones--borde.p-4(titulo="Tasa representativa del mercado (TRM)")
        .row.justify-content-center.align-items-center.mb-4
          .col-lg-12
            h4 Tasa representativa del mercado (TRM)
            p El promedio ponderado de las tasas de cambio de compra y venta de las operaciones realizadas el da hbil anterior por ciertos bancos comerciales y sociedades financieras en Bogot, Cali, Barranquilla y Medelln, segn lo calculado y publicado por la Superintendencia Bancaria de Colombia. Este dato se refleja en la pgina del banco de la repblica de Colombia.
