    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Es importante distinguir que la estrategia de marketing establece los objetivos generales y la direccin de las actividades de marketing en los prximos meses y aos, apoyados en un plan de marketing que proporcionar los detalles de las actividades reales que llevar a cabo para lograr estos objetivos. Tanto la estrategia de marketing como el plan de marketing son claves para su xito a largo plazo, ya sea que su empresa sea una pequea empresa emergente o una organizacin global. 
        p Con lo descrito, se relaciona la siguiente caracterizacin de la estrategia de marketing:

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-5
        figure
          img(src='@/assets/curso/temas/tema1/img81.svg', alt='Imagen decorativa')
      .col-lg-5
        LineaTiempoD.color-acento-botones
          .row(numero="1" titulo="Metas u objetivos")
            .col-md-12.mb-4.mb-md-0
              p.mb-0 Exprese lo que quiere lograr, a travs de la generacin de respuestas al cmo y el cundo, el tiempo que tardar en alcanzar los objetivos trazados y los recursos que demanda.
          .row(numero="2" titulo="Mercado objetivo")
