          .row(numero="7" titulo="Mtrica")
            .col-md-12.mb-4.mb-md-0
              p.mb-0 Mida cmo se est desempeando el #[i marketing] en comparacin con varios puntos de referencia, luego optimice para mejorar los resultados y el rendimiento.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p El #[i marketing] para una empresa pequea dispone de una serie de estrategias que puede utilizar para comunicarse y convencer a los clientes. Las acciones estratgicas de mercadeo pueden incluir el pago de publicidad, apariciones en eventos, campaas de relaciones pblicas, redes sociales y descuentos por correo electrnico. Como resultado, cada negocio demandar una combinacin diferente de herramientas para crear una estrategia de #[i marketing] exitosa.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-6
        figure
          img(src='@/assets/curso/temas/tema1/img82.png', alt='Imagen decorativa')
      .col-lg-6
        p La estrategia de marketing abarca la oferta, el precio, la promocin y la distribucin del producto o servicio, o la entrega del producto o servicio a sus clientes. Impulsa las caractersticas del producto, el tiempo desde el pedido hasta la entrega, la logstica, la investigacin y el desarrollo, el servicio al cliente. En resumen, impulsa lo que es clave para todas las facetas del negocio.
