    #t_1_5.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 1.5 Riesgo empresarial
    
    .bloque-texto-g.color-primario.p-3.p-sm-4.p-md-5.mb-4
      .bloque-texto-g__img(
        :style="{'background-image':`url(${require('@/assets/curso/temas/tema1/img87.png')})`}"
      )
      .bloque-texto-g__texto.p-4
        p.mb-0 Las herramientas de recoleccin de informacin son los instrumentos utilizados para recoger datos, se refiere a un cuestionario en papel o un sistema de entrevista asistido en lnea. Los estudios de casos, las listas de verificacin, las entrevistas, la observacin a veces y las encuestas o cuestionarios son herramientas que se utilizan para recopilar datos. Es importante decidir las herramientas para recolectar los datos porque la investigacin se lleva a cabo de diferentes maneras y con diferentes propsitos. El objetivo detrs de la recoleccin de datos es capturar evidencia de calidad que permita que el anlisis conduzca a la formulacin de respuestas convincentes y crebles a las preguntas planteadas.

    .row.justify-content-center.align-items-center.mb-4
      .col-lg-12
        p Para la recoleccin de los datos que fundamenta las decisiones del negocio, ha de utilizar las diferentes tcnicas cualitativas y cuantitativas que le permitan comprender el mercado, las expectativas de los clientes y la cantidad de clientes potenciales. De las tcnicas cualitativas el instrumento es la entrevista, en cuanto a las tcnicas cuantitativas el instrumento de recoleccin de datos es el cuestionario. A continuacin, se describen cada uno de estos instrumentos que se utilizan para el estudio de mercado:
    
    .h4.mb-4(data-aos="zoom-down-right")
      span.titulo-herramientas Entrevistas

    .row.justify-content-center.align-items-center.mb-4
