        p Hablar de entrevistas es comprender su naturaleza, partiendo del grado de libertad, es decir la direccin en cuanto al tema general y las preguntas precisas que formula el entrevistador. La otra naturaleza es el nivel de profundidad la cual comprende los detalles que se desean saber y que tendrn impacto en los factores del nmero de entrevistas, duracin y la interpretacin de los resultados.
